
import { useEmployeeCOEStore } from '@/store/employee/coe'
import { defineComponent, reactive } from 'vue'
import * as toastr from 'toastr';

export default defineComponent({
    setup() {
        const EmCOE = useEmployeeCOEStore();
        const form = reactive({
            date_needed: null,
            reason: null,
            compensation: false
        });

        const submitCoeRequest = async ()  => {
            await EmCOE.addEmployeeCOERequest(form);
            await EmCOE.setComponentTo('COERequestComponent')
            toastr.success('COE request added.');
        }

        return {
            EmCOE,
            form,
            submitCoeRequest
        }
    },
})
